
'use client';

import React, { useState, useEffect, useRef } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { useToast } from '@/hooks/use-toast';
import { Bot, LoaderCircle, MapPin, Search, Globe, Twitter, Newspaper, Link as LinkIcon } from 'lucide-react';
import { getLiveIncidents, LiveIncident } from '@/lib/live-incidents-service';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { Command, CommandEmpty, CommandGroup, CommandInput, CommandItem, CommandList } from '@/components/ui/command';
import { autocompletePlaces, getPlaceDetails } from '@/lib/places-service';
import { useDebounce } from '@/hooks/use-debounce';

type LocationState = {
  lat: number;
  lng: number;
  address: string;
} | null;

const SourceIcon = ({ source }: { source: LiveIncident['source'] }) => {
  switch (source) {
    case 'Web': return <Newspaper className="w-4 h-4 text-blue-500" />;
    case 'Social Media': return <Twitter className="w-4 h-4 text-sky-500" />;
    case 'Official': return <Globe className="w-4 h-4 text-green-500" />;
    default: return <LinkIcon className="w-4 h-4" />;
  }
};

export default function LiveAgentPage() {
  const [location, setLocation] = useState<LocationState>(null);
  const [incidents, setIncidents] = useState<LiveIncident[]>([]);
  const [isLocating, setIsLocating] = useState(true);
  const [isFetchingIncidents, setIsFetchingIncidents] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [autocompleteResults, setAutocompleteResults] = useState<{ description: string; placeId: string }[]>([]);
  const [isPopoverOpen, setIsPopoverOpen] = useState(false);
  
  const debouncedSearchQuery = useDebounce(searchQuery, 300);
  const { toast } = useToast();

  useEffect(() => {
    navigator.geolocation.getCurrentPosition(
      async (position) => {
        const { latitude, longitude } = position.coords;
        const geocoder = new google.maps.Geocoder();
        const response = await geocoder.geocode({ location: { lat: latitude, lng: longitude } });
        const address = response.results[0]?.formatted_address || 'Current Location';
        setLocation({ lat: latitude, lng: longitude, address });
        setIsLocating(false);
      },
      (error) => {
        console.error("Error getting location:", error);
        toast({ variant: 'destructive', title: 'Could not get your location.' });
        // Fallback location
        setLocation({ lat: 37.7749, lng: -122.4194, address: 'San Francisco, CA, USA' });
        setIsLocating(false);
      }
    );
  }, [toast]);

  useEffect(() => {
    if (location) {
      const fetchIncidents = async () => {
        setIsFetchingIncidents(true);
        const result = await getLiveIncidents(location);
        if (result.success && result.data) {
          setIncidents(result.data);
        } else {
          toast({ variant: 'destructive', title: 'Failed to fetch live incidents.' });
        }
        setIsFetchingIncidents(false);
      };
      fetchIncidents();
    }
  }, [location, toast]);

  useEffect(() => {
    if (debouncedSearchQuery.length > 2) {
      const fetchAutocomplete = async () => {
        const result = await autocompletePlaces(debouncedSearchQuery, null, ['(regions)']);
        if (result.success && result.data) {
          setAutocompleteResults(result.data);
        }
      };
      fetchAutocomplete();
    } else {
      setAutocompleteResults([]);
    }
  }, [debouncedSearchQuery]);
  
  const handleSelectLocation = async (placeId: string, description: string) => {
    const result = await getPlaceDetails(placeId);
    if (result.success && result.data) {
        setLocation({
            lat: result.data.location.lat,
            lng: result.data.location.lng,
            address: result.data.address
        });
        setSearchQuery(result.data.address);
        setIsPopoverOpen(false);
    } else {
        toast({ variant: 'destructive', title: 'Could not fetch location details.' });
    }
  };


  return (
    <div className="bg-background min-h-screen">
      <main className="container mx-auto px-4 py-8 md:py-16">
        <header className="text-center mb-8">
          <h1 className="text-4xl sm:text-5xl font-bold font-headline text-primary flex items-center justify-center gap-3">
            <Bot className="w-12 h-12" /> Live Agent
          </h1>
          <p className="mt-4 text-lg text-muted-foreground">
            Real-time incident updates from across the web for your selected area.
          </p>
        </header>

        <div className="max-w-2xl mx-auto mb-8">
            <Card>
                <CardContent className='p-4'>
                    <div className="flex items-center gap-3">
                        <MapPin className="w-5 h-5 text-primary" />
                        <Popover open={isPopoverOpen} onOpenChange={setIsPopoverOpen}>
                            <PopoverTrigger asChild>
                                <div className='relative w-full'>
                                    <Input
                                        placeholder="Search for a city or region..."
                                        value={searchQuery || location?.address || ''}
                                        onChange={(e) => setSearchQuery(e.target.value)}
                                        className="pr-10"
                                    />
                                    <Search className="absolute right-3 top-1/2 -translate-y-1/2 w-5 h-5 text-muted-foreground" />
                                </div>
                            </PopoverTrigger>
                            <PopoverContent className="w-[--radix-popover-trigger-width] p-0" align="start">
                                <Command shouldFilter={false}>
                                    <CommandInput placeholder="Type to search..." value={searchQuery} onValueChange={setSearchQuery}/>
                                    <CommandList>
                                        <CommandEmpty>No results found.</CommandEmpty>
                                        <CommandGroup>
                                        {autocompleteResults.map((result) => (
                                            <CommandItem
                                                key={result.placeId}
                                                onSelect={() => handleSelectLocation(result.placeId, result.description)}
                                                value={result.description}
                                            >
                                                {result.description}
                                            </CommandItem>
                                        ))}
                                        </CommandGroup>
                                    </CommandList>
                                </Command>
                            </PopoverContent>
                        </Popover>
                    </div>
                </CardContent>
            </Card>
        </div>

        {isLocating || isFetchingIncidents ? (
          <div className="flex justify-center items-center gap-2 text-muted-foreground text-lg py-16">
            <LoaderCircle className="w-6 h-6 animate-spin" />
            <p>{isLocating ? "Getting current location..." : "Fetching latest incidents..."}</p>
          </div>
        ) : (
          <div className="space-y-6">
            {incidents.length > 0 ? (
              incidents.map((incident) => (
                <Card key={incident.id} className="shadow-md hover:shadow-lg transition-shadow">
                  <CardHeader>
                    <div className="flex justify-between items-start">
                        <CardTitle className="font-headline text-xl flex items-center gap-3">
                            {incident.title}
                        </CardTitle>
                        <div className="flex items-center gap-2 text-sm text-muted-foreground">
                            <SourceIcon source={incident.source} />
                            <span>{incident.source}</span>
                        </div>
                    </div>
                     <CardDescription>{incident.timeAgo} &bull; {incident.location}</CardDescription>
                  </CardHeader>
                  <CardContent>
                    <p className="text-muted-foreground">{incident.summary}</p>
                    <Button variant="link" asChild className="p-0 h-auto mt-2">
                        <a href={incident.url} target="_blank" rel="noopener noreferrer">
                            View Source <LinkIcon className="w-4 h-4 ml-2" />
                        </a>
                    </Button>
                  </CardContent>
                </Card>
              ))
            ) : (
              <Card>
                <CardContent className="p-8 text-center">
                  <p className="text-muted-foreground">No live incidents found for the selected area.</p>
                </CardContent>
              </Card>
            )}
          </div>
        )}
      </main>
    </div>
  );
}
