// This is a mock function. Later, it will take a location and use an API.
export async function getLiveIncidents(location: {
  lat: number;
  lng: number;
  address: string;
}): Promise<{ success: boolean; data?: LiveIncident[] }> {
  // For now, we return the same mock data regardless of location.
  // We dynamically calculate the 'timeAgo' field.
  const dataWithTimeAgo = mockData.map(incident => ({
    ...incident,
    timeAgo: formatDistanceToNow(new Date(incident.timestamp), { addSuffix: true }),
    location: location.address, // Show the address of the search
  }));

  return { success: true, data: dataWithTimeAgo };
}
